import React from 'react';
import styled from 'styled-components';
export default function InfoCard({ header, description, color, bgcolor }) {
  return (
    <Container color={color} bgcolor={bgcolor}>
      <div>
        <h2>{header}</h2>
        <p>{description}</p>
      </div>
    </Container>
  );
}

const Container = styled.div`
  background: ${(props) => props.bgcolor};
  color: ${(props) => props.color};
  padding: 2vw;
  height: 100vh;
  display: grid;
  align-items: center;

  h2 {
    margin: 2rem 0;
  }
  p {
    max-width: 700px;
  }
  div {
    margin-left: 10%;
  }
`;
